import React, { useState } from "react";
import "./nav.css";
import logo from "../../img/HODLINFO.8f78fc06.png";
const Nav = ({ onChange }) => {
  return (
    <div className="nav">
      <div className="nav-main">
        <div className="logo">
          <a href="/">
            <div className="logo-main">
              <img src={logo} alt="logo" />
            </div>
          </a>
          <p>
            powered by <a href="">Finstreet</a>
          </p>
        </div>
        <div className="btns">
          <button>INR</button>
          <button>BTC</button>
          <button>BUY BTC</button>
        </div>
        <div className="btns2">
          <div className="circle"></div>
          <a href="">
            <div className="telegram">
              <img
                className="teleimg"
                src="data:image/png;base64,"
                alt=""
              />
              Connect Telegram
            </div>
          </a>
          <div className="switch">
            <label class="switch">
              <input type="checkbox" onChange={onChange} />
              <span class="slider round"></span>
            </label>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Nav;
